.container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 6vw;
  height: 4vw;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.circle {
  width: 1.8vw;
  height: 1.8vw;
  border-radius: 1.8vw;
  margin: 1px;
  display: inline-block;
}

.hit {
  background-color: lime;
  border: solid lime 0.105vw;
}

.blow {
  background-color: white;
  border: solid white 0.105vw;
}

.empty {
  background-color: black;
  border: dashed white 0.105vw;
}

.invisible {
  border: solid black 1px;
  background-color: black;
}